
.signup_sec{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 4rem;
  color: var(--secColor);
}
.signup_box{
  width: 40rem;
  background:var(--primColor);
  margin: 5rem 0;
  box-shadow: 2px 2px 10px rgba(0,0,0,0.2) ;
  padding: 1rem;
}
.login_box{
  width: 40rem;
  background:var(--primColor);
  margin: 5rem 0;
  box-shadow: 2px 2px 10px rgba(0,0,0,0.2) ;
  padding: 1rem;
}
.login_box h1{
  text-align : center;
  padding:0 0 20px 0;
  font-size: 2.2rem;
  border-bottom: 1px solid silver;
}

.signup_box h1{
  text-align : center;
  padding:0 0 20px 0;
  font-size: 2.2rem;
  border-bottom: 1px solid silver;
}
.signup_box  form{
  padding:0px;
  box-sizing: border-box;
}
form .txt_field{
  position: relative;
  border-bottom: 2px solid silver;
  margin: 30px 0;
}
.passwordEye{
   color: var(--secColor);
   background-color: var(--primColor);
}
.txt_field input{
  width:100%;
  padding:0 5px;
  height:40px;
  font-size: 16px;
  border:none;
  background:none;
  outline: none;
  color: var(--secColor);
}
.txt_field label{
  position: absolute;
  top:50%;
  left:5px;
  color:silver;
  transform: translateY(-50%);
  font-size:16px;
  pointer-events: none;
  transition: 1s;
}
.txt_field span::before{
  content: '';
  position: absolute;
  top: 40px;
  left:0;
  width:0;
  height: 2px;
  background-color: #4CA746;
  transition: .5s;
}
.txt_field input:focus ~ label,
.txt_field input:valid ~ label{
  top:-6.6px;
  color: #4CA746;
}
.txt_field input:focus ~ span::before,
.txt_field input:valid ~ span::before{
  width:100%;
}
.pass{
  margin:-5px 0 20px 5px;
  color:#a6a6a6;
  cursor:pointer;
}
.pass:hover{
  text-decoration: underline;
  color:black;
}
.signup_btn{
  width:100%;
  height:50px;
  color: #fff;
  border: 1px solid var(--primColor);
  background: var(--tertiary);
  border-radius: 0.8rem;
  font-size: 18px;
  cursor:pointer;
  font-weight: 600;
  outline:none;
}
input[type=submit]:hover{
  text-decoration: underline;
  border-color:black;
  transition: .2s;
}
.signup_link{
  margin:30px 0;
  text-align: center;
  font-size: 16px;
  color:#666666;
  cursor:pointer;
}
.signup_link a{
  color:#4CA746;
  text-decoration: none;
}
.signup_link a:hover{
    text-decoration: underline;
}
.center1{
  position: absolute;
  top: 25%;
  left: 120%;
  transform: translate(-50%, -50%);
  width: 400px;
  background:white;
  border-radius: 10px;
}
